<template>
  <main>
    <PageHeaderBanner :image-url="data?.img" :title="data?.title" mini />

    <b-container class="py-3" fluid>
      <b-row>
        <b-col cols="12" sm="8" md="7" offset-md="1" xl="8">
          <ArticlePlaceholdersPanel v-if="pending" />
          <ContentRenderer v-else :value="data">
            <ContentRendererMarkdown :value="data.body" />
          </ContentRenderer>
        </b-col>
        <b-col cols="12" sm="4" md="3" xl="2">
          <RelatedLinksSidebarPanel :busy="pending" class="mb-2 mb-md-3 mb-lg-4" :links="links" />
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script setup>
  import PageHeaderBanner from "~/components/sections/PageHeaderBanner";
  import ArticlePlaceholdersPanel from "~/components/panels/ArticlePlaceholdersPanel";
  import RelatedLinksSidebarPanel from "~/components/panels/RelatedLinksSidebarPanel";

  const links = [
    { hidden: true, title: "Bug Bounty", to: "/policies/bug-bounty" },
    { title: "Cookie Policy", to: "/policies/cookie-policy" },
    { hidden: true, title: "Ethical Concerns", to: "/policies/ethical-concerns" },
    { hidden: true, title: "Modern Slavery Statement", to: "/policies/modern-slavery-statement" },
    { title: "Privacy Policy", to: "/policies/privacy-policy" },
    { hidden: true, title: "Sustainability Policy", to: "/policies/sustainability-policy" },
    { hidden: true, title: "Terms & Conditions", to: "/policies/terms-and-conditions" }
  ];

  definePageMeta({
    layout: "blog"
  });

  const route = useRoute();

  const { data, error, pending } = await useLazyAsyncData(
    `content-${route.path}`,
    () => queryContent().where({ _path: route.path }).findOne(),
    { server: true }
  );

  if (error.value) console.log("Error: %o", error.value);

  useContentHead(data);
</script>
